.item {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1rem;
	background: var(--white);
	margin-bottom: 1.5rem;
	border-radius: 4px;}

.item:last-child{
    margin-bottom: 0;
}

.todoDetails {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	gap: 1rem;
}
.texts {
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.todoText {
	word-break: break-all;
	font-weight: 500;
	font-size: 1.4rem;
	color: var(--black-2);}

.complete {
		text-decoration: line-through;
		opacity: 0.7;
		color: white;
}

.completed{
    background-color: #b3b3b3;
	color: white;
}

.todoActions {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 1rem;
}

.icon {
	font-size: 2rem;
	padding: 0.5rem;
	border-radius: 4px;
	background-color: var(--gray-1);
	color: var(--black-2);
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: 0.3s ease background-color;}
    
	.icon:hover {
		background-color: var(--gray-2);
	}

