.button {
	display: inline-block;
	height: auto;
	padding: 0.8rem 2rem;
	border: none;
	border-radius: 6px;
	font-weight: 500;
	font-size: 1.6rem;
	text-decoration: none;
	text-transform: capitalize;
	cursor: pointer;
	overflow: hidden;

}

.button--primary {
	background-color: white;
	color: black;
}
.button--primary:hover{
	background-color: black;
	color: white;

}
.button--secondary {
	background-color: var(--bg-3);
	color: var(--black-1);
}

.button--all{
	background-color: black;
	color: white;
}

.button--all:hover{
	background-color: white;
	color: black;
}